export const createPage10MenuComp= ({
	MenuItems = ["1.", "2.","3."]
}) => {
  const article = document.createElement('article');

  const section = `

  <div id="appMenuAlone">
  <simple-menu v-bind:items="itemss"></simple-menu>
	</div>




	<script type="module" src="/U10/Vue.js"></script>
	<script type="module" src="/U10/MenuCompAlone.js"></script>


	<script>
	new Vue({
	el: '#appMenuAlone',
	data:{
		itemss: ["`+MenuItems+`"]
	}
	});
	</script>

`;

  article.insertAdjacentHTML('beforeend', section);

  return section;
};


function myFunction(arrd) {
	let result ="["

	for (let i=0; i < arrd.length; i++) {
		result+= '"' + arrd[i] + '"';
		if(i < arrd.length-1)
			result+=",";
	}
	return result + "]"
  }
//["1.", "2.","3."]